import * as React from "react"
import Layout from "/src/components/layout"

import { useTranslation } from "gatsby-plugin-react-i18next"
import { graphql } from "gatsby"

import "/src/assets/css/ironchip.scss"
import "/src/assets/css/pages/index.css"
import "/src/assets/css/mediaQueries/intervals.css"
import "/src/assets/css/mediaQueries/sm.css"

const LBFraud = () => {
  const { t } = useTranslation()

  return <Layout></Layout>
}

export default LBFraud

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(
      filter: { ns: { in: ["global", "lbfraud"] }, language: { eq: $language } }
    ) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`
